.categories {
    width: 87.5%;
    margin: 5vh auto;
    .heading {
        border-bottom: 1px solid rgb(170, 170, 170);
        h1 {
            padding-bottom: 1.25rem;
            font-size: 2.2rem;
        }
        .stack {
            color: rgb(30, 30, 30);
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 1.1rem;
            font-weight: 600;
            padding: 0.5rem 0;
            width: 8.25%;
            border-bottom: 3px solid rgb(85, 85, 85);
            margin-bottom: -2px;
            .icon {
                margin-right: 0.5rem;
            }
        }
    }
    .categories-wrapper {
        margin: 1.2rem auto;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        .category-container {
            color: rgb(60, 60, 60);
            width: 15%;
            margin: 0 0.4rem;
            display: flex;
            flex-direction: column;
            align-items: baseline;
            border-radius: 7px;
            box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.6);
            padding: 0.75rem 0.85rem;
            &:first-of-type {
                margin-left: 0;
            }
            .category-name {
                font-size: 1.1rem;
                line-height: 1.4rem;
                font-weight: 600;
                display: flex;
                align-items: center;
                justify-content: center;
                letter-spacing: 0.1px;
                .icon {
                    color: rgb(130, 130, 130);
                    margin-right: 0.75rem;
                }
            }
            .cost {
                margin: 1.25rem 0 2.5px;
                font-weight: 600;
                font-size: 1.05rem;
            }
        }
    }
}