.subscriptions {
    width: 87.5%;
    margin: 12.5vh auto;
    .heading {
        border-bottom: 1px solid rgb(170, 170, 170);
        h1 {
            padding-bottom: 1.5rem;
            font-size: 2.2rem;
        }
        .options-wrapper {
            display: flex;
            .option {
                cursor: pointer;
                color: rgb(170, 170, 170);
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 1.05rem;
                font-weight: 600;
                padding: 0.5rem 0 11px;
                width: 9.5%;
                margin-bottom: -2px;
                &.active {
                    color: rgb(30, 30, 30);
                    border-bottom: 3px solid rgb(85, 85, 85);
                    padding-bottom: 8px;
                }
                .icon {
                    margin-right: 0.5rem;
                    &.cross {
                        font-size: 0.9rem;
                    }
                }
            }
        }
    }
    .subscriptions-wrapper {
        margin: 1.2rem auto;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        .susbscriptions-categories {
            display: flex;
            flex-direction: row;
            border-bottom: 1px solid gray;
            .category-item {
                width: 20%;
                font-weight: 600;
                font-size: 14.5px;
                color: rgb(150, 150, 150);
            }
        }
    }
}